*{
  box-sizing: border-box;
}
p{
  line-height: 1.6;
  word-spacing: 2px;
}

body{
  background-color: #172b4d;
  font-family: -apple-system,Roboto,Fira Sans,Helvetica Neue,sans-serif;
  text-align: center;
  color: #fff;
}
.App {
  h1{
    font-size: 63px;
    font-weight: 400
  }
}
.Input{
  text-align: left;
  margin: 80px 170px;
  .form{
    display: flex;
    flex-direction: column;
    
  }
  label{
    margin-left: 12px;
  }
  input{
    font-size: 16px;
    margin: 6px 0;
    width: 96%;
    outline: none;
    border:none;
    padding: 15px;
    border-radius: 25px;
  }
}
.Desc{
  
  h4{
    cursor: pointer;
    text-decoration: underline;
  }
  p{
    text-align: left;
    animation: FadeIn 0.4s ease-in;
    transition: all 0.25s linear 0s;
  }
}
@keyframes FadeIn{
  from{opacity: 0;}
  to{opacity: 1;}
}
.btn{
  margin: 40px auto;
  border-radius: 25px;
  padding: 15px 50px;
  text-align: center;
  border: 1px solid #999;
  background-color: #ccc;
  color: #666;
  transition: all 0.25s linear;
  font-size: 16px;
  font-weight: 600;
  &.active{
    background-color: #3f51b5;
    color: #fff;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    
    &:hover{
    background-color: #fff;
    transform: translateY(-2px);
    box-shadow: 0 15px 30px -12px hsl(0deg 0% 100% / 20%);
    span{
    color: #5364c3;
    }
    &::before{
      height: 100%;
      z-index: -1;

    }
    }
    &::before{
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      background-color: #fff;
      width: 100%;
      height: 0;
      transition: height 0.25s ease-in;
    }
  }
}
.Result{
  margin: 0 134px;
  background-color: #1f3a67;
  padding: 40px 128px;
  border-radius: 16px;
  animation: FadeIn 0.4s ease-in;
  h1{
    font-size: 55px; 
  }
}

.result{
  &-source{
    color: #ccc;
    font-size: 12px;
  }
}
.card{
  margin: 60px auto; 
  display: inline-block;
  padding: 0 120px;
  background-color:#2e4874; 
  box-shadow: 0 2px 2px 0 rgb(0 0 0 / 14%), 0 3px 1px -2px rgb(0 0 0 / 12%), 0 1px 5px 0 rgb(0 0 0 / 20%);

  &-title {
    p{
    font-size: 25px;
  }
  .classify{
    font-size: 30px;
    font-weight: 600;
  }
  }
  .advice{
    margin: 0 -10px;
  }
}
@import './responsive'